<template>
    
  <div class="content">
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="1500" indicator-color="white">
      <van-swipe-item v-for="(item, index) in Banner" :key="index"
        ><img :src="item.banner_img" alt=""
      /></van-swipe-item>
    </van-swipe>
    <!-- 导航 -->
    <div class="index-kind">
      <ul>
        <li>
          <img
            src="data:image/png;base64,"
            alt=""
          />
          <font>特色课</font>
        </li>
        <li>
          <img
            src="data:image/png;base64,"
            alt=""
          />
          <font>一对一辅导</font>
        </li>
        <li @click="study">
          <img
            src="data:image/png;base64,"
            alt=""
          />
          <font>学习日历</font>
        </li>
      </ul>
    </div>
    <!-- 数据展示 -->
    <div v-for="(item, i) in appIndex" :key="i">
      <div v-show="item.channel_info.type == 3" class="box">
        <p class="index-page">{{ item.channel_info.name }}</p>
        <div class="ot-content" v-for="t in item.list" :key="t.teacher_id">
          <div class="ot-item" @click="teacher(t.teacher_id)">
            <img :src="t.teacher_avatar" alt="" />
            <div>
              <p>
                <span>{{ t.teacher_name }}</span>
              </p>
              <p>
                {{ t.introduction }}
              </p>
            </div>
          </div>
        </div>
      </div>
      <div v-if="item.channel_info.type == 1" class="box">
        <p class="index-page">{{ item.channel_info.name }}</p>
        <div class="index-item-page" v-for="t in item.list" :key="t.id">
          <div class="ii-item" @click="$router.push(`/kecheng?id=${t.id}`)">
            <p class="ii-title">
              <font>{{ t.title }}</font>
            </p>
            <div class="ii-time">
              <p>共{{ t.total_periods }}课时</p>
            </div>
            <div class="ii-teacher">
              <div class="teacher-item">
                <img alt="" :src="t.teachers_list[0].teacher_avatar" />
                <font>{{ t.teachers_list[0].teacher_name }}</font>
              </div>
            </div>
            <p class="ii-info">
              <span>{{ t.sales_num }}人已报名</span>
              <font style="color: #44a426" v-if="t.price == 0">免费</font>
              <font style="color: #eb6100" v-if="t.price != 0"
                >{{ t.price / 100 }}.00</font
              >
            </p>
            <img :src="t.cover_img" alt="" class="has_buy" v-if="token" />
          </div>
        </div>
      </div>
    </div>
    <!-- 未登录之前的弹框提示 -->
    <van-overlay :show="show" @click="show = false">
      <div class="wrapper" @click.stop>
        <div class="block">
          <i @click="show = false"></i>
          <img src="https://wap.365msmk.com/img/feiji.decaf161.png" alt="" />
          <p class="login-text">赶紧登录一下吧</p>
          <p class="login-message">立即预约一对一辅导，浏览更多视频课程~</p>
          <span @click="$router.push('/login')">立即登录</span>
        </div>
      </div>
    </van-overlay>
  </div>
</template>


<script>
export default {
  name: "",
  data() {
    return {
      Banner: [],
      appIndex: [],
      token: sessionStorage.getItem("token") || "",
      // 未登录的弹框
      show: false,
    };
  },
  created() {},
  mounted() {
    this.banner();
    this.appindex();
  },
  methods: {
    // 获取轮播图数据
    async banner() {
      let { data: res } = await this.$http.banner();
      this.Banner = res.data;
    },
    // 获取首页数据
    async appindex() {
      let { data: res } = await this.$http.appIndex();
      this.appIndex = res.data;
    },
    // 点击跳转讲师详情
    teacher(id) {
      console.log(id);
      if (this.token) {
        this.$router.push(`/teacher/?id=${id}`);
        return false;
      }
      this.show = true;
    },
    // 学习日历
    study() {
      if (this.token) {
        this.$router.push(`/kecheng/?id=${id}`);
        return false;
      }
      this.show = true;
    },
    // 课程详情
    cours(id) {},
  },
};
</script>

